function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        spaceBetween: 0,
        loop: false,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
    })
}

function sliderRent() {
    var swiper = new Swiper('.rent .swiper', {
        slidesPerView: '4',
        spaceBetween: 30,
        loop: false,
        navigation: {
            nextEl: '.rent .swiper-button-next',
            prevEl: '.rent .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 15,
            },
            575: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            767: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            991: {
              slidesPerView: 4,
              spaceBetween: 20,
            },
            1200: {
                spaceBetween: 30,
            },
        }
    })
}

function sliderObjects() {
    var swiper = new Swiper('.objects .swiper', {
        slidesPerView: '1',
        spaceBetween: 0,
        loop: false,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.objects .swiper-button-next',
            prevEl: '.objects .swiper-button-prev',
        },
    })
}

$(document).ready(function() {

    sliderMain();
    sliderRent();
    sliderObjects();

    // Menu
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Menu - Burger
    $('.header-mob__burger').click(function () {
        $(this).toggleClass('header-mob__burger--active')
        $('.header__wrapp').toggleClass('header__wrapp--active')
        $('body').toggleClass('fixed-body')

        $('.sidebar').toggleClass('sidebar--active')
    })

    // Sidebar - Burger
    $('.sidebar-burger').click(function () {
        $(this).toggleClass('sidebar-burger--active')
        $('.sidebar-menu').toggleClass('sidebar-menu--active')
        $('body').toggleClass('fixed-body')
    })

    // Accordeon
    $(".sidebar-item").click((function() {
        $(this).toggleClass("sidebar-item--active")
        $(this).children(".sidebar-hidden").slideToggle()}));

    // Up
    var btn = $('.up');

    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})

function onIn() {
    if (window.innerWidth > 991) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 991) {
        $(this).removeClass("header-bottom__item--active")
    }
}